import React, { useEffect, useRef, useState } from 'react';
import { getUserPage } from '@/api/AdminUser'
import { subscriptionInfoDetail, subscriptionInfoEmit } from '@/api/AdminPlan'
import dayjs from 'dayjs'
import { DatePicker, Space, Table, Tag, Switch, Checkbox, InputNumber, Button, Modal } from 'antd';
const NursingHomes = ({ item, activeTab }) => {

    const [isModalOpen, setIsModalOpen] = useState(false);
    const [columns, SetColumns] = useState(
        [
            {
                title: 'Settings',
                key: "id",

                render: (row) => (
                    <div>
                        <div className='text-text-primary text-[16px] font-semibold'>{row.SettingsName}</div>
                        <div style={{ color: "#A3AED0" }} className=' text-[13px]'>{row.SettingsLabel}</div>
                    </div>
                )
            },
            {
                title: 'value',
                // onCell:(record, rowIndex) => {
                //     return {
                //         onClick: (event) => {handelClick(record, rowIndex)}, // click row
                //     };
                //     },
                render: (row, index, key) => {
                    let content = countRef.current
                    let html = ""
                    if (key == 0) {
                        html = (
                            <div>{content?.cardNumber ? `Cards ends with ${content?.cardNumber}` : "Unsubscribed"}</div>
                        )
                    } else if (key == 1) {

                        let contractStartTime = content?.contractStartTime ? dayjs(content?.contractStartTime).format("YYYY-MM-DD") : ""
                        let contractEndTime = content?.contractEndTime ? dayjs(content?.contractEndTime).format("YYYY-MM-DD") : ""
                        html = (
                            <div className='flex items-center'>

                                {
                                    <div className='flex items-center'>

                                        <div>{contractStartTime || ""}-{contractEndTime}  </div>
                                        <Button type="link" onClick={() => emitData(content)}>edit</Button>

                                    </div>
                                }

                            </div>
                        )
                    } else {
                        html = (
                            <>
                                <p>
                                    <Checkbox onChange={(e) => handleChange(e)} checked={countRef?.current?.nextSubscriptionAction == 0 ? false : true} >
                                        {/* nextSubscriptionAction 0未选择 1选中 */}
                                        Auto Renew with same price monthly
                                    </Checkbox>

                                </p>

                            </>

                        )
                    }
                    return html
                }


            },


        ]
    )
    const data = [

        {
            SettingsName: "Payment Method",
            SettingsLabel: "Added",

        },
        {
            SettingsName: "Contract Duration",
            SettingsLabel: "Duration of Contract",

        },
        // {
        //     SettingsName: "After Contract Expires",
        //     SettingsLabel: "Duration of Contract",

        // },

    ]

    const [config, setConfig] = useState({})
    const [modelQeury, setModelQeury] = useState({})
    const countRef = useRef();
    const timerRef = useRef(true)
    const handleOk = () => {

        let row = { ...modelQeury }
        if (row.diffDays == 0) {
            return
        }
        let endtimer = row.contractStartTime.add(row.diffDays, "year")

        row.endtimer = endtimer
        row.contractStartTime = row.contractStartTime.format('YYYY-MM-DD HH:mm:ss')
        row.contractEndTime = endtimer.format('YYYY-MM-DD HH:mm:ss')
        subscriptionInfoEmit({ ...row })
            .then(data => {
                setIsModalOpen(false)
                getlist()

            })
    }

    const emitData = (row0) => {

        let row = { ...row0 }
        // 定义两个日期
        const { contractStartTime, contractEndTime } = row
        let date1
        let date2
        if (!contractStartTime) {
            date1 = dayjs()
            date2 = dayjs()
        } else {
            date1 = dayjs(row.contractStartTime);
            date2 = dayjs(row.contractEndTime);
        }


        // 计算两个日期之间的天数差异
        const diffDays = date2.diff(date1, 'year'); // 'day' 指定差异的单位是天
        row.diffDays = diffDays
        row.contractStartTime = date1
        setModelQeury(row)
        setIsModalOpen(true)
        console.log("diffDays", diffDays);
    }
    const handleChange = (row) => {
        let nextSubscriptionAction = countRef.current.nextSubscriptionAction
        let config1 = { ...countRef.current, nextSubscriptionAction: nextSubscriptionAction == 0 ? 1 : 0 }
        console.log("countRef", countRef.current)
        if (!config1.contractStartTime) {
            config1.contractStartTime = dayjs().format('YYYY-MM-DD HH:mm:ss')
            config1.contractEndTime = dayjs().add("year", 1).format('YYYY-MM-DD HH:mm:ss')
        }

        subscriptionInfoEmit({ ...config1 })
            .then(data => {
                getlist()
            })

    }

    const getlist = () => {
        console.log("item", item)
        subscriptionInfoDetail({ userId: item.id })
            .then(data => {

                let nextSubscriptionAction = data.data.nextSubscriptionAction
                console.log("data.data", data.data)

                setConfig({ ...data.data })
                let bo = nextSubscriptionAction == 0 ? false : true
                countRef.current = { ...data.data }
                console.log(bo)

            })
    }
    // useEffect(() => {
    //     if (activeTab == "Nursing Homes") {
    //         getlist()
    //     }
    // }, [activeTab])
    useEffect(() => {

        getlist()
    }, [])
    return (
        <div className=" bg-white rounded-2xl">
            <div className=" px-[22px] py-6 flex items-start flex-col lg:flex-row justify-between gap-2">
                <div className=" flex items-center justify-between w-full">
                    <h2 className=" text-2xl font-bold text-text-primary"> {item?.firstName}’s Settings</h2>

                    {/* ========show button mobile view===== */}

                </div>

            </div>
            <div className=" grid grid-cols-1">
                <Table columns={columns} dataSource={data} className='newTable' />
            </div>
            <Modal
                open={isModalOpen}
                footer={false}
                onCancel={() => setIsModalOpen(false)}>
                <div className='p-[20px]'>
                    <div className='text-[16px] font-[600]' >Set Contract Duration</div>
                    <div className='mt-[20px]'>
                        <Space>
                            <DatePicker value={modelQeury.contractStartTime} onChange={(e) => setModelQeury({ ...modelQeury, contractStartTime: e })} />
                            <InputNumber placeholder='Please enter the year' min={1} value={modelQeury.diffDays} onChange={(e) => setModelQeury({ ...modelQeury, diffDays: e })} />
                        </Space>
                    </div>
                    <div className='mt-[20px] flex justify-end'>
                        <Button onClick={() => handleOk()}>确定</Button>
                    </div>
                </div>
            </Modal>

        </div>
    )
}

export default NursingHomes;