import React, { useEffect, useState, useRef } from 'react'
import CustomModal from '@/Shared/modal/CustomModal'
import { useForm, Controller } from 'react-hook-form';
import toast from 'react-hot-toast';
import CustomToast from '@/Shared/Tosat/CustomToast';
import CustomInput from '@/Shared/input/CustomInput';
import Select from '@/Shared/Select/index';
import { addUser, updateUser, getUserPage } from '@/api/AdminUser'
import { addPlan, updatePlan } from '@/api/AdminPlan'
import { isEmptyObject } from '@/utils/comFunction'
import DeleteModal from '@/Shared/delete/DeleteModal'
import CustomErrorToast from "@/Shared/Tosat/CustomErrorToast";
import { Radio } from 'antd'


const CreatePlan = ({ modalOPen, setModalOpen, role = "", getlist, item = {}, distributorId = "", changeParams = {} }) => {

    const {
        register,
        handleSubmit,
        formState: { errors },
        reset,
        setValue,
        getValues,
        watch,
        control
    } = useForm({
        name: item?.name,
        startBit: item?.startBit,
        endBit: item?.endBit,
        price: item?.price,
        currency: item?.currency,
    });
    const watchedValue = watch("Nursing Home");
    const [title, setTitle] = useState("")
    const [okbtn, setOkbtn] = useState("")
    const [cstatus, setCstatus] = useState("create")
    const [deleteModal, setDeleteModal] = useState(false)
    const [delRole, setDelRole] = useState("")

    useEffect(() => {


        if (modalOPen) {
            reset()

            if (!isEmptyObject(item)) {
                setDelRole(item.role)
                const { price, currency, salesAgentCommission,distributorCommission ,id } = item
          
                setValue("price", price)
                setValue("currency", currency)
                setValue("salesAgentCommission", salesAgentCommission)
                setValue("distributorCommission", distributorCommission)
                setValue("id", id)
                
                setCstatus("update")
            } else {
                setCstatus("create")
                setValue("currency", "USD")

            }

            if (isEmptyObject(item)) {
                setTitle("Configure Plan")
                setOkbtn("Configure Plan")
            } else {
                setTitle("Edit Plan")
                setOkbtn("Save")
            }


        }


    }, [modalOPen])

    // 使用 useEffect 监听特定值的变化
    useEffect(() => {
        // 在这里执行你想要进行的操作，比如打印值
        const specificValue = getValues('Nursing Home');
        if (specificValue) {
            let row1 = NursingList.find(e => e.label == specificValue)
            getsupportAgentIdUser(row1?.key)
        }
    }, [watchedValue]); // 传递 getValues 作为 useEffect 的依赖项
    const getQuest = (query) => {
        return new Promise((resolve, reject) => {
            getUserPage(query)
                .then(data => {
                    let records = data.data.records

                    let list = records.map(e => {
                        return {
                            key: e.id,
                            label: e.firstName
                        }
                    })

                    // setNursingList(list)
                    resolve(list)

                })
        })
    }
    const getsupportAgentIdUser = (id) => {

        getQuest(
            {
                "current": 1,
                "size": 100,
                
                nursingHomeId: id
            }
        ).then(data => {
            setAgentIdUserList(data)



        })
    }





    const onSubmit = (data1) => {
        console.log("data1", data1)
     
        let changeParams1 = { ...changeParams,userId:distributorId }
        let data = { ...data1 }
        // nursingHomeId
        data.price=Number(data.price).toFixed(2)
        data.distributorCommission=Number(data.distributorCommission).toFixed(2)
        data.salesAgentCommission=Number(data.salesAgentCommission).toFixed(2)
        data.cycle=0
        console.log("data",data)

        if (!isEmptyObject(item)) {
            updatePlan({ ...data, ...changeParams1 })
                .then(data => {

                    if (data?.code == "00000") {
                        toast.custom((t) => (
                            <CustomToast t={t} text="Modified successfully" />
                        ));
                        setModalOpen(false)
                        getlist()
                        reset()
                    } else {
                        toast.custom((t) => (
                            <CustomErrorToast t={t} text={data.msg} />
                        ));
                    }
                })
        } else {
            addPlan({ ...data, role, ...changeParams1 })
                .then(data => {
              
                    if (data?.code == "00000") {
                        toast.custom((t) => (
                            <CustomToast t={t} text="New Plan has been created Successfully!" />
                        ));
                        setModalOpen(false)
                        getlist()
                        reset()
                    } else {
                        toast.custom((t) => (
                            <CustomErrorToast t={t} text={data.msg} />
                        ));
                    }
                })
        }


    };


    return (
        <CustomModal
            modalOPen={modalOPen}
            setModalOpen={setModalOpen}
            handleSubmit={handleSubmit(onSubmit)}
            width={590}
            title={title}
            cstatus="xxx"
            id={item?.id}

            buttonText={okbtn}
        >
            {/* <div className='flex items-center gap-4'>
                <CustomInput
                    label={"Plan name"}
                    type={"text"}
                    register={
                        register("name", {
                            required: {
                                value: true,
                                message: "Plan 1",
                            },
                        })
                    }
                    error={errors.name}
                    placeholder={"Enter Plan Name"}
                />
            </div> */}
            {/* <div className='flex items-center gap-4'>
                <CustomInput
                    label={"From"}
                    type={"number"}
                    register={
                        register("startBit", {
                            required: {
                                value: true,
                                message: "Plan starts from (amount of beds)",
                            },
                        })
                    }
                    error={errors.startBit}
                    placeholder={"Enter starts from"}
                />
                <CustomInput
                    label={"To"}
                    type={"number"}
                    register={
                        register("endBit", {
                            required: {
                                value: true,
                                message: "Plan ends from (amount of beds)",
                            },
                        })
                    }
                    error={errors.endBit}
                    placeholder={"Enter ends from"}
                />
            </div> */}
            <div className='flex items-center gap-4'>
                <CustomInput
                    label={"Price"}
                    type={"number"}
                    step="0.01"
                    register={
                        register("price", {
                            required: {
                                value: true,
                                message: "39.99",
                            },
                        })

                    }
                    error={errors.price}
                    placeholder={"Enter price"}
                />
                <CustomInput
                    label={"Currency"}
                    type={"text"}
                    disabled={true}
                    register={
                        register("currency", {
                            required: {
                                value: true,
                                message: "$ - US Dollars",
                            },
                        })
                    }
                    
                    error={errors.currency}
                    placeholder={"Enter currency"}
                />
            </div>
            <div className='flex items-center gap-4'>
                <CustomInput
                    label={"salesAgentCommission"}
                    type={"number"}
                    step="0.01"
                    register={
                        register("salesAgentCommission", {
                            required: {
                                value: true,
                                message: "39.99",
                            },
                        })
                    }
                    error={errors.salesAgentCommission}
                    placeholder={"Enter salesAgentCommission"}
                />
                <CustomInput
                    label={"distributorCommission"}
                    type={"text"}
                    register={
                        register("distributorCommission", {
                            required: {
                                value: true,
                                message: "Enter distributorCommission",
                            },
                        })
                    }
                    error={errors.currency}
                    placeholder={"Enter distributorCommission"}
                />
            </div>
            {/* <Controller
                name="cycle"
                control={control}
                rules={{ required: 'This field is required' }}
                render={({ field }) => (
                    <div className="flex flex-col items-start w-full mt-3">
                        <label
                            htmlFor="otp"
                            className="mb-1 font-medium text-[13px] text-[#1B2559]"
                        >
                         Cycle
                        </label>

                        <Radio.Group onChange={e => field.onChange(e.target.value)} value={field.value}>
                            <Radio value="1">Month</Radio>
                            <Radio value="2">Year</Radio>
                        </Radio.Group>
                   </div>
                )}
            /> */}
            {/* <div className='flex items-center gap-4'>
                <Radio.Group
                    register={
                        register("cycle", {
                            required: {
                                value: true,
                                message: "change cycle",
                            },
                        })
                    }
                >
                    <Radio value={1}>month</Radio>
                    <Radio value={2}>year</Radio>

                </Radio.Group>
            </div> */}
        </CustomModal>
    )
}

export default CreatePlan