import React, { useEffect, useRef, useState } from 'react';
import { getPlanPage } from '@/api/AdminPlan'
import CustomButton from '@/Shared/button/CustomButton'

import Sort from '@/Shared/sort/Sort';


import AdminSupportAgentTable from '@/Components/PubTable/AdminSupportAgentTable';
import AdminSupportAgentTableAction from '@/Components/PubTable/AdminSupportAgentTableAction';
import PlanTableAction from '@/Components/PubTable/PlanTableAction';

import AdminSwith from './AdminSwith.jsx'
import { SidebarContext } from '@/Context/CustomUsertable'
import throttle from '@/utils/throttle'
import AddIcon from '@/Components/AddIcon'
import { Space, Table, Tag, Switch } from 'antd';
import CreatePlan from '@/Components/PubTable/CreatePlan';
const NursingHomes = ({ item, activeTab }) => {
    const [page, SetPage] = useState({
        "current": 1,
        "size": 10,

    })
    const [loading, setLoding] = useState(true)
    const [search, setSearch] = useState("");
    const [selected, setSelected] = useState("");
    const [modalOPen, setModalOpen] = useState(false)
    const [query, setQuey] = useState({
        role: "Configure Plan",
        userId: item?.id,
        order: "1",
        orderType: "time",
    })
    const [totle, SetTotle] = useState(0)
    const [list, SetList] = useState([])
    const [columns, SetColumns] = useState(
        [

            {
                title: 'Price',
                render: (row) => <button>
                    <span className=' text-[14px] xl:text-base  font-normal text-text-secondary'>{row.price}</span>
                </button>,
            },
            {
                title: 'Currency',
                render: (row) => <span className=' text-[14px] xl:text-base  font-normal text-text-secondary'>{row.currency}</span>

            },

            {
                title: 'salesAgentCommission',
                render: (row) => <span className=' text-[14px] xl:text-base  font-normal text-text-secondary'>{row.salesAgentCommission}</span>
            },
            {
                title: 'cycle',
                render: (row) => <span className=' text-[14px] xl:text-base  font-normal text-text-secondary'>{row.cycle==0?"Month":"Year"}</span>
            },
            {
                title: 'distributorCommission',
                render: (row) => <span className=' text-[14px] xl:text-base  font-normal text-text-secondary'>{row.distributorCommission}</span>
            },

            {
                title: "",
                key: "id",
                render: (row) => (
                    <div className='flex items-center'>
                        {
                            row.status == 1 ? <AdminSwith setLoding={setLoding} row={row} getlist={getlist} distributorId={query.userId} />
                                :
                                <div className='flex items-center'>
                                    <AdminSwith setLoding={setLoding} row={row} getlist={getlist} distributorId={query.userId} />
                                    <PlanTableAction getlist={getlist} data={row} distributorId={query.userId} />
                                </div>

                        }


                    </div>

                )
            }
        ]
    )
    const data1 = [
        {
            Invoice: "Bill 01/24",
            Balance: "- $15400",
            status: "0",
            Billedto: "Jaime Purdy",
            PaymentDue: "1/15/2024",
        },
        {
            Invoice: "Bill 01/23",
            Balance: "- $15400",
            status: "1",
            Billedto: "Jaime Purdy",
            PaymentDue: "1/15/2024",
        },
        {
            Invoice: "Bill 01/22",
            Balance: "- $15400",
            status: "0",
            Billedto: "Jaime Purdy",
            PaymentDue: "1/15/2024",
        }
    ]
    const columns1 = [
        {
            title: 'Invoice',
            key: "id",

            render: (row) => <span className=' text-[14px] xl:text-base  font-normal text-text-secondary'>{row.Invoice}</span>
        },
        {
            title: 'Balance',

            render: (row) => <button>
                <span className={`text-[14px] xl:text-base  font-normal  ${row.status == 0 ? "text-onBackWarring" : "text-green"} `}>{row.Balance}</span>
            </button>,
        },
        {
            title: 'Billed to',

            render: (row) => <span className=' text-[14px] xl:text-base  font-normal text-text-secondary'>{row.Billedto}</span>

        },
        {
            title: ' User Status',

            render: (row) => <span className=' text-[14px] xl:text-base  font-normal text-text-secondary'>{row.Billedto}</span>

        },
        {
            title: ' Invoice',

            render: (row) => <div className='flex items-center '>
                <div>
                    <svg xmlns="http://www.w3.org/2000/svg" width="15" height="11" viewBox="0 0 15 11" fill="none">
                        <path d="M7.32723 10.1819C2.82892 10.1819 0.177932 5.92399 0.0671119 5.74311C0.00345881 5.63824 -0.0134848 5.51918 0.0103279 5.40836C-0.0139428 5.29571 0.00391675 5.17298 0.0707754 5.06765C0.185259 4.88631 2.93104 0.621094 7.32677 0.621094C11.8251 0.621094 14.4761 4.87898 14.5869 5.05987C14.6505 5.16474 14.6675 5.2838 14.6437 5.39462C14.6679 5.50727 14.6501 5.63 14.5832 5.73532C14.4687 5.91713 11.723 10.1819 7.32723 10.1819ZM0.944518 5.41065C1.43588 6.12869 3.78647 9.26647 7.32723 9.26647C10.7943 9.26647 13.203 6.11587 13.7099 5.39279C13.2186 4.67474 10.868 1.53697 7.32723 1.53697C3.8602 1.53697 1.45145 4.68711 0.944518 5.41065ZM7.32723 8.01218C5.87283 8.01218 4.68952 6.82888 4.68952 5.37447C4.68952 3.92007 5.87283 2.73676 7.32723 2.73676C8.78164 2.73676 9.96494 3.92007 9.96494 5.37447C9.96494 6.82888 8.78164 8.01218 7.32723 8.01218ZM7.32723 3.65309C6.37793 3.65309 5.60539 4.42563 5.60539 5.37493C5.60539 6.32423 6.37793 7.09677 7.32723 7.09677C8.27653 7.09677 9.04907 6.32423 9.04907 5.37493C9.04907 4.42563 8.27653 3.65309 7.32723 3.65309Z" fill="#333951" />
                    </svg>
                </div>
                <div className='ml-[10px]'>
                    <svg xmlns="http://www.w3.org/2000/svg" width="14" height="14" viewBox="0 0 14 14" fill="none">
                        <path d="M14 13.125C14 13.6082 13.6082 14 13.125 14H0.875C0.391754 14 0 13.6082 0 13.125C0 12.6418 0.391754 12.25 0.875 12.25H13.125C13.6082 12.25 14 12.6418 14 13.125ZM6.38129 10.3531C6.55216 10.524 6.77605 10.6094 7 10.6094C7.22389 10.6094 7.44789 10.524 7.61871 10.3531L10.7189 7.25296C11.0606 6.91124 11.0606 6.35723 10.7189 6.01552C10.3772 5.6738 9.82316 5.6738 9.48145 6.01552L7.875 7.62196V0.875C7.875 0.391754 7.48325 0 7 0C6.51675 0 6.125 0.391754 6.125 0.875V7.62196L4.51855 6.01552C4.17684 5.6738 3.62283 5.6738 3.28111 6.01552C2.9394 6.35723 2.9394 6.91124 3.28111 7.25296L6.38129 10.3531Z" fill="black" />
                    </svg>
                </div>
            </div>

        },

        {
            title: "",
            key: "id",
            width: 250,
            render: (row) => (
                <div className='flex items-center'>
                    <div className='flex items-center rounded-[36px] bg-[#E2E3E5] pt-[6px] pb-[6px] pl-[10px] pr-[10px]'>
                        <div className=''>
                            <svg xmlns="http://www.w3.org/2000/svg" width="13" height="14" viewBox="0 0 13 14" fill="none">
                                <circle cx="6.5" cy="7" r="5" fill="#E8E9EE" stroke="white" stroke-width="3" />
                            </svg>
                        </div>
                        <div className='ml-[6px] text-white text-[13px]'>Subscribe here</div>
                    </div>

                    <AdminSupportAgentTableAction distributorId={query.userId} getlist={getlist} data={row} />
                </div>
            )
        }
    ]
    const data = ["Time", "Name", "Price", "Status"]

    const handelOpenModal = () => {
        setModalOpen(true)
    }
    const getlist = () => {
        throttle(() => {
            setLoding(true)
            getPlanPage({ ...page, ...query }).then(res => {
                setLoding(false)
                if (res.data) {
                    let records = res.data.records.map((item, index) => {
                        item.index = index + 1
                        return item
                    })
                    SetTotle(res.data.total)
                    SetList(res.data.records)
                }
            })
        })
    }

    useEffect(() => {
        getlist()
    }, [page])

    useEffect(() => {
        if (selected && query.orderType != selected) {
            setQuey({ ...query, ...{ orderType: selected.toLowerCase(),order: (selected === "Time") ? "1":"0" } })
            SetPage({ ...page, current: 1 })
        }
    }, [selected])

    useEffect(() => {
        SetPage({ ...page, current: 1 })
    }, [query.order])

    return (
        <div className=" bg-white rounded-2xl">
            <div className=" px-[22px] py-6 flex items-start flex-col lg:flex-row justify-between gap-2">
                <div className=" flex items-center justify-between w-full">
                    <h2 className=" text-2xl font-bold text-text-primary"> {item?.firstName}’s Subscription Plans</h2>

                    {/* ========show button mobile view===== */}

                </div>
                <div className=" flex items-center justify-end gap-5 w-full">
                    {/* =====Search sort and add admin button===== */}
                    {/* <Sort selected={selected} setSelected={setSelected} data={data} /> */}
                    <Sort
                        query={query}
                        setQuey={setQuey}
                        selected={selected}
                        setSelected={setSelected}
                        data={data} />
                    <CustomButton onClick={() => handelOpenModal()} className={" hidden lg:flex min-w-[80px]"}>
                        <AddIcon />{" "}
                        Add New Plan
                    </CustomButton>
                </div>
            </div>
            <div className=" grid grid-cols-1">
                {/* <Table columns={columns} dataSource={superAdmin} className='newTable' /> */}

                <SidebarContext.Provider value={{ getlist, query, distributorId: query.distributorId, totle, page, SetPage }}>
                    <AdminSupportAgentTable loading={loading} tableData={list} getlist={getlist} columns={columns} />
                </SidebarContext.Provider>
            </div>
            <CreatePlan
                distributorId={query.userId}
                item={{}}
                getlist={getlist}
                role={query.role}
                modalOPen={modalOPen}
                setModalOpen={setModalOpen} />

            {/* <div>
                <div className=" px-[22px] py-6 flex items-start flex-col lg:flex-row justify-between gap-2">
                    <div className=" flex items-center justify-between w-full">
                        <h2 className=" text-2xl font-bold text-text-primary"> Billing History</h2>

                     

                    </div>

                </div>
                <div className=" grid grid-cols-1">
                    <Table columns={columns1} dataSource={data1} className='newTable' />
                </div>
            </div> */}

        </div>
    )
}

export default NursingHomes;