                //     title: "Total Revenue",
                //     text: "$" + (card.monthlyRevenue || 0)
                // },
                // {
                //     svg: `<svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" viewBox="0 0 16 16" fill="none">
