import React, { useDebugValue, useEffect, useRef, useState } from "react";
import { getUserPage } from "@/api/AdminUser";
import CustomButton from "../../Shared/button/CustomButton";
import SearchInput from "../../Shared/Search/SearchInput";
import Sort from "../../Shared/sort/Sort";

import CreateAdminSupportAgent from "@/Components/PubTable/CreateAndEditUsers";
import AdminSupportAgentTable from "@/Components/PubTable/AdminSupportAgentTable";
import AdminSupportAgentTableAction from "@/Components/PubTable/AdminSupportAgentTableAction";
import AdminFiled from "@/Shared/AdminFiled/AdminFiled";

import { SidebarContext } from "@/Context/CustomUsertable";
import throttle from "@/utils/throttle";
import AddIcon from "@/Components/AddIcon";
import ls from "store2";
import { useNavigate } from "react-router-dom";
import { useCountStore } from "@/store/index";
const NursingHomes = ({ item, activeTab }) => {
  const { setUserDetil } = useCountStore();
  const navigate = useNavigate();
  const isInitialized = useRef(false);
  const [page, SetPage] = useState({
    current: 1,
    size: 10,
  });
  const [role, setRole] = useState("");
  const [search, setSearch] = useState("");
  const [selected, setSelected] = useState("");
  const [modalOPen, setModalOpen] = useState(false);
  const [query, setQuey] = useState({
    role: "Nursing Home",
    parentId: item?.id,
    order: "1",
    orderType: "time",
    name: "",
  });
  const [totle, SetTotle] = useState(0);
  const [list, SetList] = useState([]);
  const [columns, SetColumns] = useState([
    {
      title: "Nursing Home",
      key: "id",
      onCell: (record, rowIndex) => {
        return {
          onClick: (event) => {
            handelClick(record, rowIndex);
          }, // click row
        };
      },
      render: (row) => (
        <button>
          <AdminFiled data={row} name={row.businessName} />
        </button>
      ),
    },
    {
      title: "Contact Number",

      render: (row) => (
        <button>
          <span className=" text-[14px] xl:text-base  font-normal text-text-secondary">
            {row.contactNumber}
          </span>
        </button>
      ),
    },
    {
      title: "Business Address",

      render: (row) => (
        <span className=" text-[14px] xl:text-base  font-normal text-text-secondary">
          {row.businessAdress}
        </span>
      ),
    },
    {
      title: "Support Agents",
      render: (row) => (
        <span className=" text-[14px] xl:text-base  font-normal text-text-secondary">
          {row.supportAgents}
        </span>
      ),
    },
    {
      title: "Elderlies",
      render: (row) => (
        <span className=" text-[14px] xl:text-base  font-normal text-text-secondary">
          {row.elderlies}
        </span>
      ),
    },

    {
      title: "",
      key: "id",
      render: (row) => (
        <AdminSupportAgentTableAction getlist={getlist} data={row} />
      ),
    },
  ]);
  const data = ["Time", "Name"];
  const handelClick = (record) => {
    console.log("111");
    setUserDetil(record);
    ls.set("tableData", record);
    let t = setTimeout(() => {
      navigate(`/support-agent/dashboard/suspended-user/${record.id}`);
      clearTimeout(t);
    }, 0);
  };
  const handelOpenModal = () => {
    setModalOpen(true);
  };
  const getlist = () => {
    throttle(() => {
      setRole(query.role);

      getUserPage({ ...page, ...query }).then((res) => {
        console.log("Query==============>");
        console.log(query);
        if (res.data) {
          SetTotle(res.data.total);
          SetList(res.data.records);
          console.log("Data===========>");
          console.log(res.data);
        }
      });
    });
  };

  useEffect(() => {
    getlist();
  }, [page, columns]);
  useEffect(() => {
    if (selected && query.orderType != selected) {
      setQuey({
        ...query,
        ...{
          orderType: selected.toLowerCase(),
          order: selected === "Time" ? "1" : "0",
        },
      });
      SetPage({ ...page, current: 1 });
    }
  }, [selected]);
  useEffect(() => {
    SetPage({ ...page, current: 1 });
  }, [query.order]);
  const handBlurchange = () => {
    setQuey({ ...query, name: search.trim() });
    SetPage({ ...page, current: 1 });
  };
  return (
    <div className=" bg-white rounded-2xl">
      <div className=" px-[22px] py-6 flex items-start flex-col lg:flex-row justify-between gap-2">
        <div className=" flex items-center justify-between w-full">
          <h2 className=" text-2xl font-bold text-text-primary">
            {" "}
            {item?.firstName || item?.businessName}’s Nursing Homes
          </h2>

          {/* ========show button mobile view===== */}
        </div>
        <div className=" flex items-center justify-end gap-5 w-full">
          {/* =====Search sort and add admin button===== */}
          <SearchInput
            search={search}
            setSearch={setSearch}
            handBlurchange={handBlurchange}
            placeholder="Search Nursing Home"
          />
          <Sort
            query={query}
            setQuey={setQuey}
            selected={selected}
            setSelected={setSelected}
            data={data}
          />
          <CustomButton
            onClick={() => handelOpenModal()}
            className={" hidden lg:flex min-w-[80px]"}
          >
            <AddIcon /> Add New Nursing Home
          </CustomButton>
        </div>
      </div>

      {/* =============Admin Support Agent data table============== */}
      <div className=" grid grid-cols-1">
        <SidebarContext.Provider
          value={{
            role,
            getlist,
            query,
            distributorId: query.distributorId,
            totle,
            page,
            SetPage,
          }}
        >
          <AdminSupportAgentTable
            tableData={list}
            getlist={getlist}
            columns={columns}
          />
        </SidebarContext.Provider>
      </div>
      <CreateAdminSupportAgent
        distributorId={query.distributorId}
        item={{}}
        getlist={getlist}
        role={role}
        modalOPen={modalOPen}
        setModalOpen={setModalOpen}
      />
    </div>
  );
};

export default NursingHomes;
