import request from '../utils/request'

export function getPlanPage(data) {
    return request({
        url: '/admin/plan/page',
        method: 'post',
        data
    })
}

export function addPlan(data) {
    return request({
        url: '/admin/plan/insert',
        method: 'post',
        data
    })
}

export function updatePlan(data) {
    return request({
        url: '/admin/plan/update',
        method: 'post',
        data
    })
}

export function deletPlan(data) {
    return request({
        url: `/admin/plan/delete?id=${data.id}`,
        method: 'post',
        data
    })
}

export function subscriptionInfoDetail(data) {
    return request({
        url: `/admin/subscriptionInfo/details`,
        method: 'post',
        data
    })
}

export function subscriptionInfoEmit(data) {
    return request({
        url: `/admin/subscriptionInfo/edit`,
        method: 'post',
        data
    })
}

export function planEnable(data) {
    return request({
        url: '/admin/plan/enable',
        method: 'post',
        data
    })
}

export function calcleDisable(data) {
    return request({
        url: '/admin/plan/disable',
        method: 'post',
        data
    })
}
