import React, { useEffect, useRef, useState } from "react";
import Overview from "./Overview";
import NursingHomes from "./NursingHomes";
import Agents from "./Agents";
import SupportAgents from "./SupportAgents";
import { useroverViewCount, userGetById } from "@/api/AdminDashboard";
import { Spin } from "antd";
import TabMenus from "@/Components/TabMenus";
import arrowRight from "@/assets/arrowRight.svg";
import Subscriptions from "./components/Subscriptions";
import Setting from "./components/Setting";
import ls from "store2";
import BlockButton from "@/Components/BlockButton";
import CreateAdminSupportAgent from "@/Components/PubTable/CreateAndEditUsers";
import { useCountStore } from "@/store/index";
import { NavLink, useParams, useNavigate } from "react-router-dom";
import dayjs from "dayjs";
const DetailOfElderly = () => {
  let query = useParams();
  const UserDetil = useCountStore((state) => state.UserDetil);
  const navigate = useNavigate();
  const [modalOPen, setModalOpen] = useState(false);
  const [changeParams, setChangeParams] = useState({});
  const [activeTab, setActiveTab] = useState("Overview");
  const [item, setItem] = useState({});

  const [card, setCard] = useState([]);
  const [spinning, setSpinning] = useState(false);
  const [user, SetUser] = useState(ls.get("user"));
  const [menus, setMenus] = useState([
    {
      text: "Overview",
      bo: true,
      warring: false,
    },
    {
      text: "Nursing Homes",
      bo: true,
      warring: false,
    },
    {
      text: "Agents",
      bo: true,
      warring: false,
    },
    // {
    //     text: "Subscriptions",
    //     value: "Subscriptions",
    //     bo: true,
    //     warring: false
    // },
    // {
    //     text: "Settings",
    //     value: "Settings",
    //     bo: true,
    //     warring: false
    // },
  ]);
  const [EditBtn, setEditBtn] = useState("");

  const [chardata, setChardata] = useState({
    series: [],
    data: [],
  });
  const HandleTabText = (e) => {
    setActiveTab(e);
  };
  const handleBack = () => {
    navigate(-1);
  };

  const init = async () => {
    setSpinning(true);

    let datas = await userGetById({ id: query.id });

    const { role, id } = datas.data;
    setItem(datas.data);
    let data = await useroverViewCount({ id: id });
    setCard(data?.data);
    console.log(data);

    setEditBtn(`Edit ${role} `);
    if (role == "Nursing Home") {
      if (user.role == "Sales Agent") {
        setMenus([
          {
            text: "Support Agents",
            value: "Support Agents",
            bo: true,
            warring: false,
          },
        ]);
        setActiveTab("Support Agents");
      } else if (user.role == "Super Admin") {
        setMenus([
          {
            text: "Overview",
            bo: true,
            warring: false,
          },
          {
            text: "Support Agents",
            bo: true,
            warring: false,
          },
          // {
          //     text: "Subscriptions",
          //     value: "Subscriptions",
          //     bo: true,
          //     warring: false
          // },
          // {
          //     text: "Settings",
          //     value: "Settings",
          //     bo: true,
          //     warring: false
          // }
        ]);
        setActiveTab("Overview");
      } else {
        setMenus([
          {
            text: "Overview",
            value: "Overview",
            bo: true,
            warring: false,
          },
          {
            text: "Support Agents",
            value: "Support Agents",
            bo: true,
            warring: false,
          },
          // {
          //     text: "Subscriptions",
          //     value: "Subscriptions",
          //     bo: true,
          //     warring: false
          // },
          // {
          //     text: "Settings",
          //     value: "Settings",
          //     bo: true,
          //     warring: false
          // },
        ]);
      }
    } else if (role == "Sales Agent") {
      setMenus([
        {
          text: "Overview",
          value: "Overview",
          bo: true,
          warring: false,
        },
        {
          text: "Nursing Homes",
          value: "Nursing Homes",
          bo: true,
          warring: false,
        },
        // {
        //     text: "Subscriptions",
        //     value: "Subscriptions",
        //     bo: true,
        //     warring: false
        // },
        // {
        //     text: "Settings",
        //     value: "Settings",
        //     bo: true,
        //     warring: false
        // },
      ]);
    } else if (role == "Distributor") {
      if (user.role == "Super Admin") {
        setMenus([
          {
            text: "Overview",
            bo: true,
            warring: false,
          },
          {
            text: "Nursing Homes",
            bo: true,
            warring: false,
          },
          {
            text: "Agents",
            bo: true,
            warring: false,
          },
          // {
          //     text: "Subscriptions",
          //     value: "Subscriptions",
          //     bo: true,
          //     warring: false
          // },
          // {
          //     text: "Settings",
          //     value: "Settings",
          //     bo: true,
          //     warring: false
          // }
        ]);
        setActiveTab("Overview");
      }
    }
    //
    setSpinning(false);
  };

  useEffect(() => {
    setMenus([]);

    init();

    //  console.log(item);
  }, [query.id]);

  return (
    <Spin spinning={spinning}>
      <div className="pl-[20px] pr-[50px]">
        <div className="h-[100px] flex items-center justify-between">
          <div className="flex items-center ">
            <img
              className="cursor-pointer"
              src={arrowRight}
              onClick={() => {
                handleBack();
              }}
            />
            <div className="text-[28px] font-semibold ml-[50px] font-cblock ">
              {item?.firstName !== null
                ? item?.firstName + " " + item?.lastName
                : item?.businessName}
            </div>
          </div>
          <div>
            <BlockButton onClick={() => setModalOpen(true)}>
              <svg
                xmlns="http://www.w3.org/2000/svg"
                width="14"
                height="14"
                viewBox="0 0 14 14"
                fill="none"
              >
                <path
                  d="M8.65073 2.31039L11.4807 5.14035L4.31724 12.3038L1.48886 9.47387L8.65073 2.31039ZM13.6281 1.62787L12.3661 0.365808C11.8783 -0.121936 11.0863 -0.121936 10.5969 0.365808L9.38799 1.57474L12.218 4.40473L13.6281 2.99457C14.0064 2.61625 14.0064 2.00617 13.6281 1.62787ZM0.00787518 13.4516C-0.0436269 13.6834 0.165643 13.8911 0.397455 13.8347L3.551 13.0701L0.722618 10.2401L0.00787518 13.4516Z"
                  fill="white"
                />
              </svg>
              <div className="pl-[20px]  text-white text-[14px] font-semibold  ">
                {EditBtn}
              </div>
            </BlockButton>
          </div>
        </div>
        <div className="pt-[20px] pb-[10px] flex">
          <TabMenus
            menus={menus}
            activeTab={activeTab}
            setActiveTab={setActiveTab}
          />
        </div>

        {activeTab === "Overview" && (
          <>
            {<Overview item={item} user={user} card={card} role={item?.role} />}
          </>
        )}
        {activeTab === "Nursing Homes" && (
          <>{<NursingHomes activeTab={activeTab} item={item} />}</>
        )}
        {activeTab === "Agents" && (
          <>{<Agents activeTab={activeTab} item={item} />}</>
        )}
        {activeTab === "Support Agents" && (
          <>{<SupportAgents activeTab={activeTab} item={item} />}</>
        )}
        {activeTab === "Subscriptions" && (
          <>{<Subscriptions activeTab={activeTab} item={item} />}</>
        )}
        {activeTab === "Settings" && (
          <>{<Setting activeTab={activeTab} item={item} />}</>
        )}
        <CreateAdminSupportAgent
          changeParams={changeParams}
          item={item}
          handleClose={handleBack}
          getlist={() => init()}
          role={item?.role}
          modalOPen={modalOPen}
          setModalOpen={setModalOpen}
        />
      </div>
    </Spin>
  );
};

export default DetailOfElderly;
