
import Ellipse from '@/assets/icon/Ellipse.svg'
import green from '@/assets/icon/EllipseGreen.svg'
import toast from "react-hot-toast";
import CustomErrorToast from "@/Shared/Tosat/CustomErrorToast";
import { useState } from 'react'
import { planEnable, calcleDisable } from '@/api/AdminPlan'
export default function adminSwith({ row ,distributorId,getlist,setLoding}) {
    const [bo, setBo] = useState(row.status == 0 ? false : true)
    const handleChange = (index) => {
        let status = index ? 1 : 0
        setLoding(true)
       
        let api = index ? planEnable : calcleDisable
      
            api({
                "userId": distributorId,
                "id": row.id
            })
            .then(data=>{
                setLoding(false)
                if(data.code=="00000"){
                    getlist()
                    setBo(index)
                }else{
                    toast.custom((t) => (
                        <CustomErrorToast
                          t={t}
                          text={data.msg}
                          title={"Error"}
                        />
                      ));
                }
              

            })
        
    }
    return (
        <div className='flex items-center h-full'>
            {
                bo == 0 ? <div className='flex items-center h-full'>
                    <div onClick={() =>handleChange(true)} className=" h-[28px]  flex justify-end mr-[10px] items-center bg-[#E2E3E5] rounded-[20px] pl-[10px] pr-[10px] ursor-pointer">
                        <div className='w-[13px] h-[13px] bg-[#fff] rounded-full'>
                            <img src={Ellipse} />
                        </div>
                        <div className=" rounded-full  pr-[10px] pl-[10px] flex  ">
                            Subscribe here
                        </div>
                    </div>
                </div> : <>
                    <div onClick={() => handleChange(false)} className=" h-[28px]  flex justify-end mr-[10px] items-center text-[#fff] bg-[#01BE4F] rounded-[20px] pl-[10px] pr-[10px] ursor-pointer">
                        <div className='w-[13px] h-[13px] bg-[#fff] rounded-full'>
                            <img src={green} />
                        </div>
                        <div className=" rounded-full  pr-[10px] pl-[10px] flex  ">
                            Active
                        </div>
                    </div>
                </>
            }


        </div>
    )
}