import React,{useEffect,useState} from 'react';
import ReactEcharts from 'echarts-for-react';
import 'echarts/lib/chart/bar';


function MonthChart({chardata}) {
   const [option, setOption] = useState({
   
});
useEffect(() => {
   
    if(chardata.xdata.length>0){
        // setOption({
        //     ...option,
        //     xAxis:{
        //         ...option.xAxis,
        //         data:chardata.xdata
        //     },
        //     series: [
        //         {
        //             color: "#34CECE",
        //             data: chardata.serdata,
        //             type: 'line',
        //             areaStyle: {}
        //         }
        //     ],
        // })
        setOption(
            {
                tooltip: {
                    show: false,
            
                },
                legend: {
                    show: true,
                },
                grid: {
                    left: '3%',
                    right: '4%',
                    bottom: '3%',
                    containLabel: true
                },
                xAxis: {
                    type: 'category',
                    boundaryGap: false,
                    data: chardata.xdata,
                    axisLine: {
                        show: true // 不显示 x 轴线
                    },
                    splitLine: {
                        show: false // 不显示 x 轴的分割线
                    }
                },
                yAxis: {
                    type: 'value',
                    axisLine: {
                        show: true
                    },
                    splitLine: {
                        show: false // 不显示 x 轴的分割线
                    }
                },
                dataZoom:[
                    {
                        type:"inside"
                    }
                ],
                color: ["#34CECE", "#FA4B5E", "#FAB515"],
                series: [
                    {
                        color: "#34CECE",
                        data:chardata.serdata,
                        type: 'line',
                        symbol: 'none', // 添加这一行
                        areaStyle: {}
                    }
                ]
            }
        )
    }else{
     
        setOption({
            title: {
                text: "There is currently no data available",
                show:  true,
                left: "center",
                top: "center",
                textStyle: {
                  color: "#000",
                  fontSize: "20px",
                  fontWeight: 400,
                },
              },
        })
    }
    
},[chardata])
   

    return <ReactEcharts notMerge={true} option={option}  />;
}

export default MonthChart;